import React, { Component } from 'react'
import './App.css'
import {data} from './data'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import Checkbox from '@material-ui/core/Checkbox'
import Paper from '@material-ui/core/Paper'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import FlipMove from 'react-flip-move'
import SwapVertIcon from '@material-ui/icons/SwapVert'

const stopPropagation = e => e.stopPropagation();

class App extends Component {
  state = {
    data,
  } 
  reverse = () => {
    const data = this.state.data.slice().reverse();
    this.setState({data});
  }
  selectItem = id => {
    const index = this.state.data.findIndex(({address}) => address === id);
    const data = this.state.data.slice();
    data[index].selected = true;
    this.setState({data});
  }
  onReorder = (id, checkedItems, after) => {
    const selectedItems = Array.from(checkedItems);
    const data = this.state.data.filter(({address}) => !checkedItems.has(address));
    let index = data.findIndex(({address}) => address === id);
    if (after)
      index++;
    data.splice(index, 0, ...selectedItems.map(address => ({address})));
    this.setState({data});
  }
  onItemClick = id => {
    const targetIndex = this.state.data.findIndex(({address}) => address === id);
    this.setState({targetIndex});
  }
  render() {
    const {data} = this.state;
    console.log(this.state);
    return (
      <div className="App">
        <AppBar position="sticky">
          <Toolbar>
            <IconButton color="inherit" onClick={this.reverse}>
              <SwapVertIcon />
            </IconButton>
          </Toolbar>        
          {/* <Button onClick={this.reverse}>Reverse</Button> */}
        </AppBar>
        <AddressList data={data} onReorder={this.onReorder} onItemClick={this.onItemClick}/>
      </div>
    );
  }
}

class AddressList extends Component {
  state = {
    checkedItems:new Set(),
    targetIndex: null
  }
  toggleChecked = id => {
    let checkedItems = new Set(this.state.checkedItems);
    if (checkedItems.has(id))
      checkedItems.delete(id);
    else
      checkedItems.add(id);
    this.setState({checkedItems});
  }
  onReorder = id => {
    this.props.onReorder(id, this.state.checkedItems);
    setTimeout(() => this.setState({checkedItems: new Set()}), 3000);
  }
  onItemClick = id => {
    let targetIndex = this.props.data.findIndex(({address}) => address === id);
    if (this.state.targetIndex === targetIndex || !this.state.checkedItems.size)
      targetIndex = null;
    this.setState({targetIndex});
  }
  onMoveBefore = id => {
    this.props.onReorder(id, this.state.checkedItems, false);
    setTimeout(() => this.setState({checkedItems: new Set()}), 1500);
  }
  onMoveAfter = id => {
    this.props.onReorder(id, this.state.checkedItems, true);
    setTimeout(() => this.setState({checkedItems: new Set()}), 1500);
  }
  render() {
    const { checkedItems, targetIndex } = this.state;
    console.log(this.state);
    const data = this.props.data.map(data => {
      data.selected = checkedItems.has(data.address);
      return data;
    });
    return (
      <FlipMove>
        {
          data.map(({address, selected}, index) => 
            <AddressListItem 
              key={address} 
              id={address} 
              address={address} 
              selected={selected} 
              target={targetIndex === index}
              onClick={this.onItemClick} 
              onReorder={this.onReorder}
              toggleChecked={this.toggleChecked}
              onMoveBefore={this.onMoveBefore}
              onMoveAfter={this.onMoveAfter}
            />
          )
        }
      </FlipMove>        
    )
  }
}

class AddressListItem extends Component {
  onClick = () => this.props.onClick(this.props.id)
  toggleChecked = () => this.props.toggleChecked(this.props.id)
  onMoveBefore = () => this.props.onMoveBefore(this.props.id);
  onMoveAfter = () => this.props.onMoveAfter(this.props.id);
  render() {
    const { id, address, selected, target } = this.props;
    console.log(this.props);
    return <Paper key={id} elevation={0} square style={{backgroundColor:selected && "#FDD"}}>
      {!target  
      ? <ListItem onClick={this.onClick} style={{height:60}}>
            <ListItemText primary={address}/>
            <Checkbox checked={selected} onChange={this.toggleChecked} onClick={stopPropagation}/>
        </ListItem>         
      : <ListItem style={{height:60, backgroundColor:"#FBB7", transition: "all 0.5s ease"}} onClick={this.onClick}>
          <ListItemText primary="Move selected items"/>
          <Button color="secondary" onClick={this.onMoveBefore}>Above</Button>
          <Button color="secondary" onClick={this.onMoveAfter}>Below</Button>
        </ListItem>          
      }
    </Paper>
  }
}

export default App;



